/* ==========================================================================
   Design System
   Icons
   ========================================================================== */
.cf-icon-svg {
  height: 1.1875em;
  vertical-align: text-top;
  fill: currentcolor;
}
.cf-icon-svg__updating,
.cf-icon-svg__updating-round {
  animation: updating-animation 1.25s infinite linear;
  transform-origin: 50% 50%;
}
@keyframes updating-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
html[lang='ar'] .cf-icon-svg__right,
html[lang='ar'] .cf-icon-svg__right-round,
html[lang='ar'] .cf-icon-svg__left,
html[lang='ar'] .cf-icon-svg__left-round,
html[lang='ar'] .cf-icon-svg__arrow-right,
html[lang='ar'] .cf-icon-svg__arrow-right-round,
html[lang='ar'] .cf-icon-svg__arrow-left,
html[lang='ar'] .cf-icon-svg__arrow-left-round,
html[lang='ar'] .cf-icon-svg__help,
html[lang='ar'] .cf-icon-svg__help-round,
html[lang='ar'] .cf-icon-svg__book,
html[lang='ar'] .cf-icon-svg__book-round,
html[lang='ar'] .cf-icon-svg__document,
html[lang='ar'] .cf-icon-svg__document-round,
html[lang='ar'] .cf-icon-svg__edit,
html[lang='ar'] .cf-icon-svg__edit-round,
html[lang='ar'] .cf-icon-svg__paper-clip,
html[lang='ar'] .cf-icon-svg__paper-clip-round,
html[lang='ar'] .cf-icon-svg__cart,
html[lang='ar'] .cf-icon-svg__cart-round,
html[lang='ar'] .cf-icon-svg__disability,
html[lang='ar'] .cf-icon-svg__disability-round,
html[lang='ar'] .cf-icon-svg__travel,
html[lang='ar'] .cf-icon-svg__travel-round,
html[lang='ar'] .cf-icon-svg__bullhorn,
html[lang='ar'] .cf-icon-svg__bullhorn-round,
html[lang='ar'] .cf-icon-svg__chart,
html[lang='ar'] .cf-icon-svg__chart-round,
html[lang='ar'] .cf-icon-svg__list,
html[lang='ar'] .cf-icon-svg__list-round,
html[lang='ar'] .cf-icon-svg__external-link,
html[lang='ar'] .cf-icon-svg__external-link-round {
  transform: scaleX(-1);
}
